
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  opacity: 1;
}
#app.spinning {
  opacity: 0.4;
}
table {
  width: 100%; }

p {
  margin: 1rem 0;
  text-align: left; }

ul {
  margin: 1em 0 1em 40px; }

ul li p {
    margin: 0; }

blockquote {
  margin: 1rem 0; }

blockquote p::first-letter {
    font-size: 110%; }

h1, h2, h3, h4, h5, h6, strong {
  font-weight: bold;
  margin: 2.33em 0;
  outline: 0; }

h1 {
  font-size: 2rem; }

h2 {
  font-size: 1.8rem; }

h3 {
  font-size: 1.6rem; }

h4 {
  font-size: 1.4rem; }

h5 {
  font-size: 1.2rem; }

h6 {
  font-size: 1rem; }

code {
  border: none !important; }

dl {
  margin: 1rem 0; }

dt {
  margin-top: 1rem;
  font-weight: bold; }

dd {
  font-size: 90%; }

dd p {
  margin: 0; }

abbr {
  text-decoration: none;
  font-style: italic;
  font-family: serif;
  cursor: help; }

summary {
  outline: 0;
  cursor: pointer; }

input[type=checkbox] {
  margin-right: 8px; }

.red {
  color: red; }

[title] {
  position: relative;
  display: inline; }

[title]:after {
    content: attr(title);
    color: #fff;
    background: #333;
    background: rgba(51, 51, 51, 0.9);
    padding: 5px 10px;
    position: absolute;
    left: -9999px;
    opacity: 0;
    bottom: 100%;
    width: 350px;
    -webkit-transition: 0.25s linear opacity; }

[title]:hover:after {
    left: 5px;
    opacity: 1; }

.capitalize {
  text-transform: capitalize;
}
.navbar {
  font-size: 16px;
}
.navbar p {
  margin: 1em 0;
}
.logo {
  -webkit-animation: logo-spin infinite 20s linear;
          animation: logo-spin infinite 20s linear;
}
.spinning .logo-wrapper {
  -webkit-animation: flickerAnimation 1s infinite;
  animation: flickerAnimation 1s infinite;
}
@-webkit-keyframes logo-spin {
from { -webkit-transform: rotate(0deg); transform: rotate(0deg);
}
to { -webkit-transform: rotate(360deg); transform: rotate(360deg);
}
}
@keyframes logo-spin {
from { -webkit-transform: rotate(0deg); transform: rotate(0deg);
}
to { -webkit-transform: rotate(360deg); transform: rotate(360deg);
}
}
@keyframes flickerAnimation {
0%   { opacity:1;
}
50%  { opacity:0;
}
100% { opacity:1;
}
}
@-webkit-keyframes flickerAnimation{
0%   { opacity:1;
}
50%  { opacity:0;
}
100% { opacity:1;
}
}


