  margin-top: 6px;
  margin-bottom: 6px;
  line-height: 18px;
}
.toc a {
  color: inherit;
  text-align: left;
  text-decoration: none;
  font-size: 12px;
}
.toc > ul ul {
  padding-left: 0px;
}
.toc > ul ul ul {
  padding-left: 15px;
}
.toc > ul > li > a {
  font-size: 16px;
  opacity: 0.6;
}
.toc > ul ul a {
  font-size: 14px;
}
.toc > ul ul ul a {
  font-size: 12px;
}
